.dashboard-container {
  height: 100vh;
  margin: 30px;
  .alert {
    color: green;
    text-align: left;
    padding: 0px 0px 20px 0px;
  }
  .dashboard-links {
    display: flex;
    flex-direction: row;
    a {
      margin-right: 50px;
      text-decoration: none;
      color: black;
    }
  }
  .list-experience {
    display: flex;
    flex-direction: column;
  }
  .delete {
    margin: 20px;
    color: white;
    background-color: #ff4b2b;
    border-style: none;
    height: 5vh;
  }
}
